import React, { useEffect, useState } from 'react';
import { Row, Col, Card, Badge } from 'antd';
import Flex from 'components/shared-components/Flex';
import DonutChartWidget from 'components/customs/DonutChartWidget';
import api from '../../../utils/api';
import './styles.css';
import { THEME_CONFIG } from '../../../configs/AppConfig';
import moment from 'moment';

const COLOR_1 = '#3e82f7';  // blue
const COLOR_2 = '#04d182';  // cyan
const COLOR_3 = '#ff6b72';  // volcano
const COLOR_4 = '#ffc107';  // gold
const COLOR_5 = '#a461d8';  // purple
const COLOR_6 = '#fa8c16';  // orange
const COLOR_7 = '#17bcff';  // geekblue

export const COLORS = [
  COLOR_1, COLOR_2, COLOR_3, COLOR_4, COLOR_5, COLOR_6, COLOR_7];

// ========================================================================= //
//                          DIVERGENCES BY CARRIER                           //
// ========================================================================= //

const DivergencesWidget = (props) => {

  const data = props?.data?.divergencias;

  let labels = [];
  let series = [];
  let colors = [];
  let combined = [];

  if (data) {
    data.map((x, idx) => {

      const colorIdx = idx < 6 ? idx : 7;

      const transform = x.transportadora.split(' ');
      const transportadora = transform[0];

      labels.push(transportadora);
      series.push(x.total_divergencias);
      colors.push(COLORS[colorIdx]);

      combined.push({
        label: transportadora,
        data: x.total_divergencias,
        color: COLORS[colorIdx],
      });

      return x;
    });
  }

  return (<Card title="DIV. POR TRANSPORTADORA" className="card-divergence">

    <DonutChartWidget
      labels={labels}
      series={series}
      customScale={0.8}
      heigth={series.length === 0 && 328}
      customOptions={{ colors: colors }}
      bodyClass="mb-2 mt-3"
      extra={<Row justify="center">
        <Col xs={20} sm={20} md={20} lg={24}>
          <div className="mt-4 mx-auto" style={{ maxWidth: 200 }}>
            {combined.map(elm => (
              <Flex alignItems="center" justifyContent="between" className="mb-3" key={elm.label}>
                <div>
                  <Badge color={elm.color}/>
                  <span className="text-gray-light">{elm.label}</span>
                </div>
                <span className="font-weight-bold text-dark">{elm.data}</span>
              </Flex>))}
          </div>
        </Col>
      </Row>}
    />

    {/*
      <DonutChartWidget
        labels={labels}
        series={series}
        customScale={1}
        heigth={276}
        customOptions={{ colors: colors }}
        bodyClass="mb-2 mt-3"
        extra={<Row justify="center">
          <Col xs={20} sm={20} md={20} lg={24}>
            <div className="mt-4 mx-auto" style={{
              maxWidth: 200,
            }}>
              {combined.map(elm => (
                <Flex alignItems="center" justifyContent="between" className="mb-3" key={elm.label}>
                  <div>
                    <Badge color={elm.color}/>
                    <span className="text-gray-light">{elm.label}</span>
                  </div>
                  <span className="font-weight-bold text-dark">{elm.data}</span>
                </Flex>))}
            </div>
          </Col>
        </Row>}
      /> */}

  </Card>);
};

