// ************************************************************************* //
//                             COMPONENT WIDGET                              //
// ************************************************************************* //

const CarrierDashboard = () => {

  const [data, setData] = useState();
  const [lastRefresh, setLastRefresh] = useState(moment());
  const [load, setLoad] = useState(false);

  useEffect(() => {

    let timer;
    let timeout;
    const URI = '/carrier';

    const execApi = async (runnow) => {

      if (THEME_CONFIG.dataRefresh === '5min') {

        timeout = 300000;

      } else if (THEME_CONFIG.dataRefresh === '1min') {

        timeout = 60000;

      } else {  // 5sec

        timeout = 5000;

      }

      if (!runnow) {
        const diff = moment().diff(moment(lastRefresh), 'seconds');
        if (diff < (timeout / 1000)) return;
        setLastRefresh(moment());
      }

      await api
        .get(URI)
        .then((response) => setData(response.data?.doc))
        .catch((err) => console.error(
          `ERRO AO ACESSAR SERVIDOR: ${localStorage.getItem('baseURL')}\n\n`,
          err));
    };

    if (!load) (async () => await execApi(true))();
    setLoad(true);

    timer = setInterval(async () => await execApi(), timeout);
    return () => clearTimeout(timer);

  }, [lastRefresh, load]);

  return (<>

    <Row gutter={16}>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <DivergenceReasonWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <DivergenceVolumesWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <DivergencesWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraAtivaWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraBraspressWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraTranslovatoWidget data={data}/>
      </Col>
    </Row>

    <Row gutter={16}>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraMiraWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraJonasWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraTranswarWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraTransgenioWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraGewtWidget data={data}/>
      </Col>
      <Col xs={24} sm={24} md={12} lg={12} xl={4} xxl={4}>
        <TransportadoraIblWidget data={data}/>
      </Col>
    </Row>


  </>);
};

export default CarrierDashboard;
