import React from 'react';
import { Card } from 'antd';
import ApexChart from 'react-apexcharts';
import jh from 'jarvisly-helper';
import { apexPieChartDefaultOption } from 'constants/ChartConstant';
import PropTypes from 'prop-types';
import './styles.css';

const defaultOption = jh.clone(apexPieChartDefaultOption);

const Chart = props => {
  return (<ApexChart {...props} />);
};

const DonutChartWidget = props => {
  const {
    series,
    labels,
    extra,
    bodyClass,
    customScale,
    customOptions,
    heigth,
  } = props;

  let chartOptions = defaultOption;
  chartOptions.labels = labels;

  if (customScale) chartOptions.plotOptions.pie.customScale = customScale;
  chartOptions.plotOptions.pie.donut.labels.show = false;
  chartOptions.dataLabels.enabled = true;

  chartOptions = { ...jh.clone(chartOptions), ...jh.clone(customOptions) };

  return (<Card className="custom-donut">
    <div className={`text-center ${bodyClass}`}>
      <Chart
        type="donut" options={chartOptions} series={series}
        // width={width}
        height={heigth || '240'}/>
      {extra}
    </div>
  </Card>);
};

DonutChartWidget.propTypes = {
  series: PropTypes.array.isRequired,
  labels: PropTypes.array,
  title: PropTypes.string,
  extra: PropTypes.element,
  bodyClass: PropTypes.string,
};

DonutChartWidget.defaultProps = {
  series: [],
  labels: [],
  title: '',
  height: 250,
  width: '100%',
};

export default DonutChartWidget;
